:root {
  --doc-font-family: 'katex_main';
  --doc-font-family-monospace: 'Courier', monospace;
  --doc-font-size: 8pt;
  --doc-math-font-size: 1.1em;
  --doc-math-font-weight: regular;

  --ui-font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 
    'Open Sans', Ubuntu, Helvetica, Arial, sans-serif;
  --ui-font-family-monospace: "Inconsolata", "Consolas", "Menlo", "Courier", 
    "Ubuntu Mono", "Roboto Mono", monospace;
  --ui-font-size: 9pt;

  --edit-color-dark: hsl(150,50%,60%);
  --edit-color-light: hsl(150,60%,80%);
  --edit-color-faded: hsl(150,60%,98%);


  /* light mode (default) */
  
  --ui-page: white;
  
  --ui-bg-0: white;
  --ui-bg-1: #fafafa;
  --ui-bg-2: #f5f5f5;
  
  --ui-fg-0: #555;
  --ui-fg-1: black;
  --ui-fg-2: black;
  
  --ui-border-0: #ddd;
  --ui-border-1: #ccc;
  --ui-border-2: #888;
  
  --ui-gradient-v-1: linear-gradient(white, 50%, #f5f5f5);
  --ui-gradient-v-2: linear-gradient(white, 50%, #eeeeee);
  --ui-gradient-v-x: linear-gradient(#555, 50%, #aaa);
}

/* activated by setting body class (no ui to do so yet) */
.ui-dark-mode {
  --ui-bg-0: #444;
  --ui-bg-1: #333;
  --ui-bg-2: #222;
  
  --ui-fg-0: #999;
  --ui-fg-1: #aaa;
  --ui-fg-2: #ccc;
  
  --ui-border-0: #333;
  --ui-border-1: #444;
  --ui-border-2: #555;
  
  --ui-gradient-v-1: linear-gradient(#444, 50%, #222);
  --ui-gradient-v-2: linear-gradient(#555, 50%, #222);
  --ui-gradient-v-x: linear-gradient(#111, 50%, #333);

  --ui-page: #e5e1d5;

  --edit-color-dark: rgba(155,170,0,0.7);
  --edit-color-light: rgba(55,150,0,0.5);
  --edit-color-faded: rgba(55,150,0,0.05);
}

.katex {
  white-space: nowrap;
  font-size: 1.2em;
  font-size: var(--doc-math-font-size, 1.2em);
  font-weight: normal;
  font-weight: var(--doc-math-font-weight, normal);
}

.document {
  font-family: serif;
  font-family: var(--doc-font-family, serif);
  font-size: var(--doc-font-size);
  color: black;
}

.document blockquote {
  font-style: italic;
}

.document code, .document pre {
  font-family: monospace;
  font-family: var(--doc-font-family-monospace, monospace);
}

.document em > strong {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

/* TABLES */

.document table {
  border-collapse: collapse;
}

.document th {
  font-weight: bold;
}

.document th, .document td {
  border: 1px solid black;
  padding: 0.2em 0.5em;
}

.document {
  /*font-family: var(--doc-font-family, serif);
  font-size: var(--doc-font-size, 9pt);
  */
  counter-reset: question 0;
  counter-reset: question var(--doc-startnumber, 0);
  display: flex;
  flex-direction: column;
}

.blank-underline {
  display: inline-block;
  border-bottom: 1px solid black; 
  margin: 0 1mm;
}

/* .blank-underline-heading */

.answerblanks > tbody > tr > td {
  border: none;
}

.answerblanks > tbody > tr > td:first-child {
  text-align: right;
}

.answerblank {
  min-width: 1cm;
  min-height: 1cm;
  width: 1in;
  height: 1.2cm;
  border: 1px solid black;
}

.answerchoice-list {
  list-style: none;
  padding: 0;
}

.answerchoice-item {
  margin: 2mm 5mm;
}

.answerchoice-list-horizontal .answerchoice-item {
  display: inline;
}

.answerchoice-item:before {
  counter-increment: answerchoice 1;
  content: counter(answerchoice, lower-alpha) ') ';
  margin-right: 2mm;
}

.answerkey {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  font-size: 1.1em;
  counter-reset: answerkey 0;
  counter-reset: answerkey var(--doc-startnumber, 0);
  margin: 0;
  padding: 0;
  position: relative;
  max-height: 90%;
}

.answerkey-item {
  margin: 1mm;
}

.answerkey-item::before {
  counter-increment: answerkey 1;
  content: counter(answerkey) '. ';
}

.page-header {
  border-collapse: collapse;
  width: 100%;
