html, body { margin: 0 }
@page { margin: 0 }

/** For screen preview **/
@media screen {
  body { background: #ccc; background: var(--ui-border-0, #ccc); }
  .page-outer {
    background: white;
    background: var(--ui-page, white);
    box-shadow: 0 .5mm 2mm rgba(0,0,0,.5);
    margin: 5mm;
  }
  .page-overflowing {
    outline: 4px dotted red;
  }
}

@media print {
  .document {
    overflow: visible;
    display: block;
  }
  .page-outer {
    page-break-inside: avoid;
    page-break-after: always;
    overflow: hidden;
  }
  .page-inner {
    position: static;
  }
}
